@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main, body {
    display: -webkit-flex;
    display: flex;
    height: 100%;
    background-color: rgb(139, 139, 139);
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    font-family: "Spruce Sans Pro";
    grid-gap: 2vw;
    gap: 2vw;
    color: white;
}

* {
    margin: 0;
}

.dummy {
    position: absolute;
    visibility: hidden;
    white-space: nowrap;
    font-size: 3.5em;
}

.container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    /* background-color: aqua; */
    height: 60vh;
    width: 30vw;
}

button {
    font-size: 2em;
    border: 1px solid rgba(0, 0, 0, 0.308);
    background-color: rgb(105, 92, 89);
    color: white;
}

button:hover {
    background-color: rgb(105, 85, 80);
}

.output {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(75, 75, 75);
    padding-right: 2vw;
    grid-column-start: 1;
    grid-column-end: 5;
    font-size: 3.5em;
}

.top3 {
    background-color: rgb(70, 64, 62);
}

.top3:hover {
    background-color: rgb(68, 58, 55);
}

.operator {
    background-color: rgb(255, 172, 64);
}

.operator:hover {
    background-color: rgb(255, 160, 36);
}

.zero {
    grid-column-start: 1;
    grid-column-end: 3;
}

.historyButton {
    position: absolute;
    top: 8.5vh;
    background-color: rgba(255, 255, 255, 0);
    border: none;
    margin-left: 0.2vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 1.5em;
}

.historyButton:hover {
    background-color: rgba(255, 255, 255, 0.39);
}

.container2 {
    display: -webkit-flex;
    display: flex;
    overflow: scroll;
    overflow-x: hidden;
    height: 60vh;
    width: 20vw;
    background-color: rgb(75, 75, 75);
    color: white;
    font-size: 3em;
}

@media screen and (max-width: 600px) {
    .container {
        width: 60vw;
    }
}
